import React, { Component } from 'react';
import Task from './Task';
import InputBox from './InputBox';
import { Droppable, Draggable } from 'react-beautiful-dnd';
import TitlePopUp from './TitlePopUp';
class Column extends Component {
    state = {
        showPopup: false,
        position: 1,
    }
    onMouseDown = (event) => {
        event.preventDefault();
        console.log(event.button);
        this.openPopup(event);
    }
    openPopup = (event) => {
        event.preventDefault();
        console.log(this.props);
        console.log(event)
        const newState = {
            showPopup: !this.state.showPopup,
            position: event.target.parentElement.getBoundingClientRect(),
            eventDOM: event.target,
        };
        this.setState(newState);
        return;
    }
    closePopup = (event) => {
        const newState = {
            ...this.state,
            showPopup: !this.state.showPopup,
        };
        this.setState(newState);
        return;
    }
    render() {
        console.log(this.props.tasks);
        return (
            <div style={overAllDivStyle}>
                <Draggable draggableId={this.props.column.id} index={this.props.index}>
                    {(provided) => (
                        <div {...provided.draggableProps} ref={provided.innerRef}>
                            <div style={divStyle2}>
                                <div style={headerColour} onDoubleClick={this.onMouseDown} {...provided.dragHandleProps}>
                                    {this.props.column.title}
                                </div>
                                <Droppable droppableId={this.props.column.id} type='task'>
                                    {(provided) => (
                                        <div ref={provided.innerRef} {...provided.droppableProps}>
                                            {this.props.tasks.map((task, index) => < Task key={task.id} task={task} index={index} columnId={this.props.column.id} editTask={this.props.editTask} deleteTask={this.props.deleteTask} />)}

                                            {provided.placeholder}
                                        </div>

                                    )}
                                </Droppable>
                                <InputBox addNote={this.props.addNote} columnId={this.props.column.id} deleteColumn={this.props.deleteColumn} />
                            </div >
                        </div>

                    )}
                </Draggable>
                {
                    this.state.showPopup ?
                        <TitlePopUp position={this.state.position} eventDOM={this.state.eventDOM} title={this.props.column.title} id={this.props.column.id} closePopup={this.closePopup.bind(this)} editColumn={this.props.editColumn} />
                        : null
                }
            </div >



        );
    }
}

const overAllDivStyle = {
    position: 'relative',
}
const headerColour = {
    fontFamily: "'Alata', sans-serif",
    padding: '1px',
    fontSize: '20px',
    color: '#252321',
}
const divStyle2 = {
    display: 'block',
    borderRadius: '4px',
    padding: '10px 10px 10px 10px',
    margin: '10px 10px 10px 10px',
    backgroundColor: '#eee',
    width: '300px',
    height: '100%',
}

export default Column;
