import React, { Component } from 'react';
import fire from '../fire';
class SignInPage extends Component {
    state = {
        showPopup: false,
        position: 1,
    }
    onSignIn = (event) => {
        console.log('idk')
        fire.auth().signOut().then(function () {
            console.log('Logged Out')

        }).catch(function (error) {
            console.log(error)
        });
        this.props.reset();
    }
    render() {
        return (
            <div>
                <div style={buttonStyle} onClick={this.onSignIn}>
                    Sign Out
                </div>
            </div>
        );
    }
}
const buttonStyle = {
    cursor: 'pointer',
    position: 'relative',
    textAlign: 'center',
    width: '100px',
    height: '30px',
    borderRadius: '2px',
    padding: '2px',
    backgroundColor: '#eee',
}

export default SignInPage;