import React, { Component } from 'react'
import { EmojiButton } from '@joeattardi/emoji-button';
class AddEmojis extends Component {
    state = {
        pickerState: 0,
    }
    trigger = (event) => {
        if (!(this.state.pickerState === 0)) {
            this.state.pickerState.destroyPicker();
            this.state.pickerState = 0;
            return;
        }
        var eventDOM = event.target;
        if (eventDOM.tagName === 'P') {
            eventDOM = event.target.parentElement;
        }
        const options = {
            showAnimation: false,
            autoHide: false,
            emojiSize: '25px',
            zIndex: '100',
            position: 'bottom-start',
        }
        const picker = new EmojiButton(options);
        this.state.pickerState = picker;
        picker.setTheme('auto')
        picker.showPicker(eventDOM)
        picker.on('emoji', selection => {
            // handle the selected emoji here
            console.log(selection.emoji);
            console.log(eventDOM.parentElement.parentElement)
            const textAreaDOM = eventDOM.parentElement.parentElement.getElementsByTagName('textarea')[0];
            textAreaDOM.value = textAreaDOM.value + selection.emoji;
        });
        picker.on('hidden', () => {
            if (!(this.state.pickerState === 0)) {
                this.state.pickerState.destroyPicker();
                this.state.pickerState = 0;
            }

        })
    }
    render() {
        return (
            <div style={divStyle} onClick={this.trigger}>
                <p>Add Emojis</p>
            </div>
        )
    }
}
const divStyle = {
    cursor: 'pointer',
    fontSize: '14px',
    textAlign: 'center',
    marginBottom: '10px',
    color: 'white',
    whiteSpace: 'nowrap',
    height: '30px',
    width: 'auto',
    backgroundColor: 'rgba(0, 0, 0, 0.7)',
    padding: '5px',
    borderRadius: '5px',
    overflow: 'hidden',
}
export default AddEmojis