import React, { Component } from 'react'
class EditLabels extends Component {

    render() {
        return (
            <div style={divStyle}>
                <p>Edit Labels</p>

            </div>
        )
    }
}

const divStyle = {
    cursor: 'pointer',
    fontSize: '14px',
    textAlign: 'center',
    marginBottom: '10px',
    color: 'white',
    whiteSpace: 'nowrap',
    height: '30px',
    width: 'auto',
    backgroundColor: 'rgba(0, 0, 0, 0.7)',
    padding: '5px',
    borderRadius: '5px',
    overflow: 'hidden',
}
export default EditLabels
