import React, { Component } from 'react';
import './App.css';
import Column from './components/Column';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import { v4 as uuidv4 } from 'uuid';
import AddColumn from './components/AddColumn';
import SignInPage from './components/SignInpage';
import * as firebase from "firebase/app";
import fire from './fire';
import SignOutButton from './components/SignOutButton';
import { Link } from 'react-bootstrap-icons';
// Get a reference to the database service
function writeUserData(userId, task) {
  fire.database().ref('/users/' + userId).set({
    fullState: task,
  });
}
var email = 'wenkang11861@gmail.com'
// var email = 's10203100@connect.np.edu.sg'
var password = 'password1'
// var password = 'password2'

class App extends Component {
  state = {
    tasks: {},
    columns: {},
    columnOrder: [],
    displaySignIn: true,
  }
  componentDidMount() {
    let currentComponent = this;
    fire.auth().onAuthStateChanged(function (user) {
      if (user) {
        console.log('Triggered')
        fire.database().ref('/users/' + fire.auth().currentUser.uid).once('value').then(function (snapshot) {
          var user = (snapshot.val()) || 'Anonymous';
          if (user !== 'Anonymous') {
            const newState = {
              ...user.fullState,
              displaySignIn: false,
            }
            currentComponent.setState(newState)
          } else {
            console.log('User Not Found/User Not in DataBase')
            const newState = {
              ...currentComponent.state,
              displaySignIn: false,
            }
            currentComponent.setState(newState)
          }

        });
      } else {
        console.log('No User')
      }
    });
  }
  reset = () => {
    const newState = {
      tasks: {},
      columns: {},
      columnOrder: [],
      displaySignIn: true,
    }
    this.setState(newState)
  }
  newAccount = () => {
    const newState = {
      tasks: {},
      columns: {},
      columnOrder: [],
      displaySignIn: false,
    }
    this.setState(newState)
  }
  addNote = (value, columnId) => {

    console.log(value, columnId);
    const uniqueId = uuidv4();
    const newTask = {
      ...this.state.tasks,
      [uniqueId]: { id: uniqueId, title: value },
    };
    var newColumn = {}
    try {
      newColumn = {
        ...this.state.columns,
        [columnId]: {
          ...this.state.columns[columnId],
          taskIds: [...this.state.columns[columnId].taskIds, uniqueId]
        }
      }
    } catch {
      newColumn = {
        ...this.state.columns,
        [columnId]: {
          ...this.state.columns[columnId],
          taskIds: [uniqueId]
        }
      }
    }

    const newState = {
      ...this.state,
      tasks: newTask,
      columns: newColumn,
    }
    console.log(newState);
    writeUserData(fire.auth().currentUser.uid, newState)
    this.setState(newState);
    return;
  }
  onDragEnd = (result) => {
    const { destination, source, draggableId, type } = result;
    console.log('lol');
    if (!destination) {
      return;
    }
    if (destination.droppableId === source.droppableId && destination.index === source.index) {
      return;
    }
    if (type === 'column') {
      const newColumnOrder = Array.from(this.state.columnOrder);
      newColumnOrder.splice(source.index, 1);
      newColumnOrder.splice(destination.index, 0, draggableId);
      const newState = {
        ...this.state,
        columnOrder: newColumnOrder,
      };
      this.setState(newState)
      return;
    }
    const start = this.state.columns[source.droppableId];
    const finish = this.state.columns[destination.droppableId];
    if (start == finish) {
      const column = this.state.columns[source.droppableId];
      const newTaskIds = Array.from(column.taskIds);

      newTaskIds.splice(source.index, 1);

      newTaskIds.splice(destination.index, 0, draggableId);

      const newColumn = {
        ...column,
        taskIds: newTaskIds
      };
      const newState = {
        ...this.state,
        columns: {
          ...this.state.columns,
          [newColumn.id]: newColumn,
        }
      };
      writeUserData(fire.auth().currentUser.uid, newState)
      this.setState(newState);
      return;
    }


    const startTaskIds = Array.from(start.taskIds);
    startTaskIds.splice(source.index, 1);
    const newStart = {
      ...start,
      taskIds: startTaskIds,
    };
    const finishTaskIds = Array.from(finish.taskIds);
    finishTaskIds.splice(destination.index, 0, draggableId);
    const newFinish = {
      ...finish,
      taskIds: finishTaskIds,
    };
    const newState = {
      ...this.state,
      columns: {
        ...this.state.columns,
        [newStart.id]: newStart,
        [newFinish.id]: newFinish,
      }
    };
    writeUserData(fire.auth().currentUser.uid, newState)
    this.setState(newState);
    return;
  }
  value = (event) => {
    console.log(event.getBoundingClientRect());
    return event.getBoundingClientRect();
  }
  addColumn = (value) => {
    const uniqueId = uuidv4();
    const newColumns = {
      ...this.state.columns,
      [uniqueId]: { id: uniqueId, title: value, taskIds: [] }
    };
    const newState = {
      ...this.state,
      columns: newColumns,
      columnOrder: [...this.state.columnOrder, uniqueId],
    };
    console.log(newState);
    writeUserData(fire.auth().currentUser.uid, newState)
    this.setState(newState);
    return;
  }
  editTask = (newTask, id) => {
    const newTasks = {
      ...this.state.tasks,
      [id]: { ...this.state.tasks[id], title: newTask }
    }
    const newState = {
      ...this.state,
      tasks: newTasks,
    }
    writeUserData(fire.auth().currentUser.uid, newState)
    this.setState(newState);
  }
  editColumn = (newColumn, id) => {
    const newColumns = {
      ...this.state.columns,
      [id]: { ...this.state.columns[id], title: newColumn }
    }
    const newState = {
      ...this.state,
      columns: newColumns,
    }
    writeUserData(fire.auth().currentUser.uid, newState)
    this.setState(newState);
  }
  deleteTask = (id, columnId) => {
    delete this.state.tasks[id]
    const newTasks = {
      ...this.state.tasks
    }
    const newColumns = {
      ...this.state.columns,
      [columnId]: {
        ...this.state.columns[columnId],
        taskIds: [...this.state.columns[columnId].taskIds.filter((task) => task !== id)]
      }
    }
    const newState = {
      ...this.state,
      tasks: newTasks,
      columns: newColumns,
    }
    console.log(newState)
    writeUserData(fire.auth().currentUser.uid, newState)
    this.setState(newState);
  }
  deleteColumn = (columnId) => {
    console.log('delete column')
    delete this.state.columns[columnId]
    const newColumns = {
      ...this.state.columns,
    }
    const newColumnOrder = [
      ...this.state.columnOrder.filter((id) => id !== columnId)
    ]


    const newState = {
      ...this.state,
      columnOrder: newColumnOrder,
      columns: newColumns
    }
    writeUserData(fire.auth().currentUser.uid, newState)
    this.setState(newState)
  }
  render() {
    return (
      <div>
        <div>
          <h1>MisterList</h1>
          <SignOutButton reset={this.reset} />
        </div>


        <div style={bannerStyle}>
          <DragDropContext onDragEnd={this.onDragEnd}>
            <Droppable droppableId='all-columns' direction='horizontal' type='column'>
              {(provided) => (
                <div style={bannerStyle} {...provided.droppableProps} ref={provided.innerRef}>
                  {this.state.columnOrder.map((columnId, index) => {
                    const column = this.state.columns[columnId];
                    var tasks = []
                    if (typeof column.taskIds !== 'undefined') {
                      tasks = column.taskIds.map(taskId => this.state.tasks[taskId]);
                    }

                    return (
                      <Column key={column.id} column={column} tasks={tasks} index={index} addNote={this.addNote} editTask={this.editTask} editColumn={this.editColumn} deleteTask={this.deleteTask} deleteColumn={this.deleteColumn} />
                    );
                  })}
                  {provided.placeholder}
                </div>
              )}
            </Droppable>

          </DragDropContext>
          <AddColumn addColumn={this.addColumn} />
        </div>
        {this.state.displaySignIn ?
          <SignInPage newAccount={this.newAccount} />
          : null
        }
        <div style={linkStyle}>
          <div>
            <a href="https://github.com/SpaceyCodes">Website by - Lee Wen Kang</a>
          </div>
        </div>
      </div >
    );
  }
}
const linkStyle = {
  fontSize: '10px',
  position: 'fixed',
  left: '50%',
  bottom: '10px',
  transform: 'translate(-50%)',
  padding: '3px',
  backgroundColor: '#eee',
  border: '3px black',
  borderRadius: '10px',
}
const stylelol = {
  height: '10px',
  width: '10px',
  fill: 'white',
}
const bannerStyle = {
  display: 'flex',
  flexDirection: 'row',
}
export default App;
