import React, { Component } from 'react';
import EditLabels from './EditLabels';
import AddEmojis from './AddEmojis';
class TitlePopUp extends Component {
    state = {
        contextMenu: 0,
        firstFocus: 0,
    }
    divStyle = () => {
        return {
            zIndex: '100',
            position: 'absolute',
            top: '0',
            left: '0',
        };

    }
    onSave = (event) => {
        const eventDOM = event.target;
        const inputDOM = eventDOM.parentElement.getElementsByTagName('textarea')[0];
        console.log(inputDOM.value);
        if (inputDOM.value === '') {
            this.props.addColumn('etc')
        } else {
            this.props.addColumn(inputDOM.value)
        }

        this.props.closePopup();
    }
    onChange = (event) => {
        const eventDOM = event.target;
        console.log(this.props.id);
        if (event.keyCode === 13) {
            if (eventDOM.value === '') {
                this.props.addColumn('etc')
            } else {
                this.props.addColumn(eventDOM.value)
            }
            this.props.closePopup();
        }


    }
    onFocus = (event) => {
        if (this.state.firstFocus === 0) {
            event.target.select();
            event.target.autoFocus = false
            const newState = {
                ...this.state,
                firstFocus: this.state.firstFocus + 1,
            }
            this.setState(newState);
        }

    }
    onContextMenu = (event) => {
        if (this.state.contextMenu === 0) {
            event.preventDefault();
            const newState = {
                ...this.state,
                contextMenu: this.state.contextMenu + 1,
            }
            this.setState(newState);
        }


    }
    render() {
        return (
            <div onContextMenu={this.onContextMenu}>
                <div style={this.divStyle()}>
                    <div style={textAreaDivStyle}>
                        <textarea style={formStyle} wrap='soft' defaultValue={this.props.title} onFocus={this.onFocus} onKeyDown={this.onChange}
                            autoFocus type="text" />
                    </div>
                    <button style={buttonStyle} onClick={this.onSave}>Save</button>
                </div>
                <div style={noManslandsDivStyle} onClick={this.onSave}></div>
            </div>


        );

    }
}
const buttonStyle = {
    zIndex: '100',
    marginLeft: '10px',
}
const textAreaDivStyle = {
    borderBottom: '1px #ccc solid',
    borderRadius: '4px',
    padding: '10px 10px 10px 10px',
    margin: '10px 10px 0px 10px',
    backgroundColor: '#eee',
    width: '300px',
    height: '44.55px',
    backgroundColor: 'white',
}
const formStyle = {
    fontFamily: "'Alata', sans-serif",
    fontSize: '20px',
    color: '#252321',
    resize: 'none',
    width: '100%',
    height: '100%',
    outline: 'none',
    border: 'none',
    overflow: 'hidden',
}
const noManslandsDivStyle = {
    zIndex: '99',
    position: 'fixed',
    left: '0',
    top: '0',
    width: '100%',
    height: '100%',
}

export default TitlePopUp;
