import React, { Component } from 'react';
import fire from '../fire';
class SignInPage extends Component {
    state = {
        showPopup: false,
        position: 1,
    }
    onSignIn = (event) => {
        const username = document.getElementById('username').value;
        const password = document.getElementById('password').value;
        fire.auth().signInWithEmailAndPassword(username, password).catch(function (error) {
            // Handle Errors here.
            var errorCode = error.code;
            var errorMessage = error.message;
            console.log(errorCode)
            console.log(errorMessage)
            // ...
        });
    }
    onCreate = () => {
        const username = document.getElementById('username').value;
        const password = document.getElementById('password').value;
        fire.auth().createUserWithEmailAndPassword(username, password).catch(function (error) {
            // Handle Errors here.
            var errorCode = error.code;
            var errorMessage = error.message;
            console.log(errorCode)
            console.log(errorMessage)
            // ...
        });
    }
    render() {
        return (
            <div style={overAllDivStyle}>
                <div style={whiteBoxStyle}>
                    <div style={temp}>
                        <div style={titleStyle}>
                            <div>
                                Trello Clone
                            </div>
                            <p style={pStyle}>
                                To try, feel free to create your account(Note: password reset system not in place)
                            </p>
                            <p style={pStyle}>
                                or use a premade preview account(Email: spaceycodes@gmail.com Password: password1)
                            </p>
                        </div>
                        <div style={contentsStyle}>
                            <div style={labelStyle}>Email:</div>
                            <input style={inputStyle} id='username' type="text" autoComplete="off" />
                            <div style={labelStyle}>Password:</div>
                            <input style={inputStyle} id='password' type="text" autoComplete="off" />
                            <div >
                                <input style={buttonStyle} type="submit" value='Sign In' onClick={this.onSignIn} />
                                <input style={buttonStyle} type="submit" value='Create Account' onClick={this.onCreate} />
                            </div>

                        </div>
                    </div>

                </div>
            </div >
        );
    }
}
const noteStyle = {

}
const pStyle = {
    fontSize: '16px'
}
const temp = {
    position: 'relative',
    width: '100%',
    height: '100%'
}
const overAllDivStyle = {
    position: 'fixed',
    left: '0',
    top: '0',
    width: '100%',
    height: '100%',
    backgroundColor: 'white',
    // backgroundColor: 'rgba(0,0,0,0.3)',
}
const whiteBoxStyle = {
    position: 'absolute',
    borderRadius: '5px',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
    width: '500px',
    height: '600px',
    backgroundColor: '#eee',
    boxShadow: '30px 0px 40px rgba(0, 0, 0, 0.2), -30px 0px 40px rgba(0, 0, 0, 0.2)',
}
const titleStyle = {
    fontSize: '25px',
    position: 'absolute',
    left: '50%',
    top: '10px',
    transform: 'translate(-50%, 0)',
}
const contentsStyle = {
    display: 'flex',
    flexDirection: 'column',
    width: '100%',
    height: '100%',
    padding: '30px',
    alignItems: 'center',
    justifyContent: 'center',
}
const labelStyle = {
    width: '100%'
}
const inputStyle = {
    marginBottom: '10px',
    width: '100%',
    height: '30px',
    color: '#333',
    fontSize: '18px',
    letterSpacing: '1px',
    display: 'block',
    border: 'rgba(0,0,0,0)',
    outline: 'none',
    borderRadius: '3px',
}
const buttonStyle = {
    width: '150px',
    height: '35px',
    marginLeft: '10px',
    marginRight: '10px',
}
export default SignInPage;