import React, { Component } from 'react';
import PopUp from './PopUp';
import { Draggable } from 'react-beautiful-dnd';
class Task extends Component {
    state = {
        showPopup: false,
        position: 1,
    }
    onEnter = (event) => {
        console.log('enter')
        const eventDOM = event.target
        eventDOM.style.backgroundColor = '#f3f3f3';
        eventDOM.style.cursor = 'pointer';
        eventDOM.getElementsByTagName('button')[0].style.opacity = '1';
        eventDOM.getElementsByTagName('button')[0].style.pointerEvents = 'auto';
    }
    onLeave = (event) => {
        console.log('leave')
        const eventDOM = event.target
        try {
            eventDOM.getElementsByTagName('button')[0].style.opacity = '0';
            eventDOM.getElementsByTagName('button')[0].style.pointerEvents = 'none';
            eventDOM.style.backgroundColor = 'white';

        } catch (error) {
            eventDOM.parentElement.style.backgroundColor = 'white';
            eventDOM.style.opacity = '0';
            eventDOM.style.pointerEvents = 'none';
        }

    }
    onMouseDown = (event) => {
        event.preventDefault();
        console.log(event.button);
        if (event.button === 2) {
            this.openPopup(event);
        }
    }
    openPopup = (event) => {
        event.preventDefault();
        console.log(this.props);
        const newState = {
            showPopup: !this.state.showPopup,
            position: event.target.parentElement.getBoundingClientRect(),
            eventDOM: event,
        };
        this.setState(newState);
        return;
    }
    closePopup = (event) => {
        const newState = {
            ...this.state,
            showPopup: !this.state.showPopup,
        };
        this.setState(newState);
        return;
    }
    render() {
        return (
            <div style={divStyle}>
                <Draggable draggableId={this.props.task.id} index={this.props.index}>
                    {(provided) => (
                        <div onDoubleClick={this.openPopup} onMouseEnter={this.onEnter} onMouseLeave={this.onLeave} onMouseDown={this.onMouseDown} onContextMenu={(e) => e.preventDefault()} {...provided.draggableProps} {...provided.dragHandleProps} ref={provided.innerRef}>

                            <p style={taskStyle}>
                                {this.props.task.title}
                                <button style={buttonStyle} onClick={this.openPopup}>üñäÔ∏è</button>
                            </p>
                            <div style={labelStyle}></div>
                        </div>)
                    }
                </Draggable>
                {this.state.showPopup ?
                    <PopUp position={this.state.position} eventDOM={this.state.eventDOM} title={this.props.task.title} columnId={this.props.columnId} id={this.props.task.id} closePopup={this.closePopup.bind(this)} editTask={this.props.editTask} deleteTask={this.props.deleteTask} />
                    : null
                }
            </div>

        );

    }
}

const divStyle = {
    position: 'relative',
}
const labelStyle = {
    position: 'absolute',
    top: '5px',
    left: '5px',
    borderRadius: '2px',
    width: '40px',
    height: '5px',
    backgroundColor: 'orange',
    pointerEvents: 'none',
}
const buttonStyle = {
    pointerEvents: 'none',
    opacity: '0',
    position: 'absolute',
    marginTop: 'auto',
    marginRight: 'auto',
    fontSize: '10px',
    top: '12px',
    right: '5px',
    textAlign: 'center',
}
const taskStyle = {
    fontSize: '16px',
    backgroundColor: 'white',
    padding: '10px',
    borderRadius: '2px',
    borderBottom: '1px #ccc solid',
    marginTop: '3px',
    marginBottom: '3px',
    wordWrap: 'break-word',
    position: 'relative',
}


export default Task;
