import React, { Component } from 'react'
class DeleteTask extends Component {
    onClick = () => {
        this.props.deleteTask(this.props.id, this.props.columnId);
    }
    render() {
        return (
            <div style={divStyle} onClick={this.onClick}>
                <p>Delete task</p>

            </div>
        )
    }
}

const divStyle = {
    cursor: 'pointer',
    fontSize: '14px',
    textAlign: 'center',
    marginBottom: '10px',
    color: 'white',
    whiteSpace: 'nowrap',
    height: '30px',
    width: 'auto',
    backgroundColor: 'rgba(0, 0, 0, 0.7)',
    padding: '5px',
    borderRadius: '5px',
    overflow: 'hidden',
}
export default DeleteTask
