import React, { Component } from 'react';
import AddPopUp from './AddPopUp';
import { Plus } from 'react-bootstrap-icons';
class AddColumn extends Component {
    state = {
        showPopup: false,
        position: 1,
    }
    openPopup = (event) => {
        event.preventDefault();
        console.log(this.props);
        console.log(event)
        const newState = {
            showPopup: !this.state.showPopup,
            position: event.target.parentElement.getBoundingClientRect(),
            eventDOM: event.target,
        };
        this.setState(newState);
        return;
    }
    closePopup = (event) => {
        const newState = {
            ...this.state,
            showPopup: !this.state.showPopup,
        };
        this.setState(newState);
        return;
    }
    onClick = (event) => {
        this.openPopup(event);
    }

    render() {
        return (
            <div style={overAllDivStyle}>
                <div style={divStyle2} onClick={this.onClick}>
                    <div style={headerColour}>
                        + Add another list
                        
                    </div>
                    
                </div >
                {
                this.state.showPopup ?
                    <AddPopUp position={this.state.position} eventDOM={this.state.eventDOM} title={''} closePopup={this.closePopup.bind(this)} addColumn={this.props.addColumn} />
                    : null
                }
            </div >
        );
    }
}
const overAllDivStyle = {
    position: 'relative',
}
const headerColour = {
    fontFamily: "'Alata', sans-serif",
    fontSize: '18px',
    color: '#252321',
    margin: 'auto',
    opacity: '1',
}
const divStyle2 = {
    cursor: 'pointer',
    borderRadius: '4px',
    padding: '10px 10px 10px 10px',
    margin: '10px 10px 10px 10px',
    backgroundColor: '#eee',
    width: '300px',
    backgroundColor: 'rgba(238,238,238,0.5)',
}

export default AddColumn;
