import React, { Component } from 'react';
import EditLabels from './EditLabels';
import AddEmojis from './AddEmojis';
import DeleteTask from './DeleteTask';
class PopUp extends Component {
    state = {
        contextMenu: 0,
        firstFocus: 0,
    }
    divStyle = () => {
        return {
            zIndex: '100',
            position: 'absolute',
            top: '0',
            left: '0',
        };

    }
    onChange = (event) => {
        const eventDOM = event.target;
        const inputDOM = eventDOM.parentElement.getElementsByTagName('textarea')[0];
        console.log(inputDOM.value);
        console.log(this.props);
        this.props.editTask(inputDOM.value, this.props.id);
        this.props.closePopup();
    }
    onFocus = (event) => {
        if (this.state.firstFocus === 0) {
            event.target.select();
            event.target.autoFocus = false
            const newState = {
                ...this.state,
                firstFocus: this.state.firstFocus + 1,
            }
            this.setState(newState);
        }

    }
    onContextMenu = (event) => {
        if (this.state.contextMenu === 0) {
            event.preventDefault();
            const newState = {
                ...this.state,
                contextMenu: this.state.contextMenu + 1,
            }
            this.setState(newState);
        }


    }
    render() {
        return (
            <div onContextMenu={this.onContextMenu}>
                <div style={this.divStyle()}>
                    <div style={outerDivStyle}>
                        <div style={textAreaDivStyle}>
                            <textarea style={formStyle} wrap='soft' defaultValue={this.props.title} onFocus={this.onFocus} autoFocus type="text" />
                        </div>
                        <div>
                            <EditLabels />
                            <AddEmojis />
                            <DeleteTask id={this.props.id} columnId={this.props.columnId} deleteTask={this.props.deleteTask} />
                        </div>

                    </div>
                    <button style={buttonStyle} onClick={this.onChange}>Save</button>
                </div>
                <div style={noManslandsDivStyle} onClick={this.props.closePopup}></div>
            </div>


        );

    }
}
const outerDivStyle = {
    display: 'flex',
    flexDirection: 'row',
}
const textAreaDivStyle = {
    padding: '10px',
    marginRight: '10px',
    borderRadius: '2px',
    width: '280px',
    height: '150px',
    backgroundColor: 'white',
    boxShadow: '0 0 0 2000px rgba(0,0,0,0.4)',
}
const buttonStyle = {
    zIndex: '100',
}
const formStyle = {
    lineHeight: '22.4px',
    fontFamily: 'Arial, Helvetica, sans-serif',
    fontSize: '16px',
    resize: 'none',
    width: '100%',
    height: '100%',
    outline: 'none',
    border: 'none',
    overflow: 'hidden',
}
const noManslandsDivStyle = {
    zIndex: '99',
    position: 'fixed',
    left: '0',
    top: '0',
    width: '100%',
    height: '100%',
}
const divStyle2 = {
    zIndex: '99',
    position: 'fixed',
    left: '0',
    top: '0',
    width: '100%',
    height: '100%',
    pointerEvent: 'none',
}


export default PopUp;
