import React, { Component } from 'react'
class InputBox extends Component {
    checkValue = (event) => {
        if (event.keyCode === 13) {
            event.preventDefault();
            let value = event.target.value;
            if (value !== '') {
                console.log(value);
                event.target.value = '';
                this.props.addNote(value, this.props.columnId);
            }

        }

    }
    checkValueSubmit = (event) => {
        event.preventDefault()
        const eventDOM = event.target;
        const inputDOM = eventDOM.parentElement.getElementsByTagName('input')[0];
        const value = inputDOM.value;
        console.log(event);
        if (!(value === '')) {
            inputDOM.value = '';
            this.props.addNote(value, this.props.columnId);
        }
    }
    onDelete = (event) => {
        event.preventDefault()
        this.props.deleteColumn(this.props.columnId)
    }
    render() {
        return (
            <div>
                <form>
                    <input type="text" onKeyDown={this.checkValue} autoComplete="off" />
                    <input type="submit" onClick={this.checkValueSubmit} />
                    <input type="submit" onClick={this.onDelete} value='Delete' />
                </form>

            </div>
        )
    }
}
export default InputBox
