import React, { Component } from 'react';
import EditLabels from './EditLabels';
import AddEmojis from './AddEmojis';
class TitlePopUp extends Component {
    state = {
        contextMenu: 0,
        firstFocus: 0,
    }
    divStyle = () => {
        return {
            zIndex: '100',
            position: 'absolute',
            top: '20px',
            left: '20px',
        };

    }
    onChange = (event) => {
        const eventDOM = event.target;
        const inputDOM = eventDOM.parentElement.getElementsByTagName('textarea')[0];
        console.log(inputDOM.value);
        console.log(this.props.id);
        this.props.editColumn(inputDOM.value, this.props.id);
        this.props.closePopup();
    }
    onFocus = (event) => {
        if (this.state.firstFocus === 0) {
            event.target.select();
            event.target.autoFocus = false
            const newState = {
                ...this.state,
                firstFocus: this.state.firstFocus + 1,
            }
            this.setState(newState);
        }

    }
    onContextMenu = (event) => {
        if (this.state.contextMenu === 0) {
            event.preventDefault();
            const newState = {
                ...this.state,
                contextMenu: this.state.contextMenu + 1,
            }
            this.setState(newState);
        }


    }
    render() {
        return (
            <div onContextMenu={this.onContextMenu}>
                <div style={this.divStyle()}>
                    <div style={textAreaDivStyle}>
                        <textarea style={formStyle} wrap='soft' defaultValue={this.props.title} onFocus={this.onFocus} autoFocus type="text" />
                    </div>
                </div>
                <div style={noManslandsDivStyle} onClick={this.onChange}></div>
            </div>


        );

    }
}
const textAreaDivStyle = {
    padding: '1px',
    borderRadius: '2px',
    borderBottom: '1px #ccc solid',
    width: '280px',
    height: '28px',
    backgroundColor: 'white',
}
const formStyle = {
    fontFamily: "'Alata', sans-serif",
    fontSize: '20px',
    color: '#252321',
    resize: 'none',
    width: '100%',
    height: '100%',
    outline: 'none',
    border: 'none',
    overflow: 'hidden',
}
const noManslandsDivStyle = {
    zIndex: '99',
    position: 'fixed',
    left: '0',
    top: '0',
    width: '100%',
    height: '100%',
}

export default TitlePopUp;
